import React, { Component } from 'react'
import Header from '../header'

let url = "https://edurestapi.herokuapp.com/login";


class login extends Component {

    constructor(props){
        super(props)

        this.state ={
            email:'',
            password:'',
            report:''
        }
    }

    handleChange=(event)=>{
        this.setState({
            [event.target.name]:event.target.value
        })
        
    }
    handleSubmit = ()=>{
        
        fetch(url,
            {
                method:'POST',
                headers:{
                    'Accept':'application/json',
                    'Content-Type':'application/json'
                },
                body:JSON.stringify(this.state)
            })
        .then((resp)=>resp.json())
        .then((data)=>{
            console.log('>>><<<',data);
            sessionStorage.setItem('ltk',data.token)

            this.props.history.push('/')
            
            
        })

        

    }
    render() {
        return (
            <div>
                <Header />
                <br/><br/>
                <div className="container">
                <div className="row">
                    <div className="col-md-6 col-sm-6 col-xs-12">
                            <img style={{borderRadius:'50%',width:'90%'}} src={`https://media.istockphoto.com/vectors/login-icon-user-icon-in-flat-style-isolated-on-red-background-key-vector-id1064173074?k=6&m=1064173074&s=170667a&w=0&h=IdD3H7WS5tc2eggFkTag7OnO4NMSzJGSypRX_S0Dluw=`} />
                    </div>

                    <div className="col-md-6 col-sm-6 col-xs-12">
                        

                        <div className="panel" style={{boxShadow:'0 3px 6px 0 rgba(0, 0, 0, 0.26)'}}>
                            <div className="panel-heading" style={{backgroundColor:'#eb361e',color:'white'}}>Login User</div>
                            <div className="panel-body">
                                <div>
                                    <div className="form-group">
                                        <label for="email">Email *</label>
                                        <input type="email" name="email" value={this.state.email} className="form-control" onChange={this.handleChange} required/>
                                    </div>
                                    <div className="form-group">
                                        <label for="password">Password *</label>
                                        <input type="password" name="password" value={this.state.password} className="form-control" onChange={this.handleChange} required/>
                                    </div>

                                    <div className="form-group">
                                    
                                        <button onClick={this.handleSubmit} className="form-control btn btn-danger btn-block" >Login</button>
                                    </div>

                        
                                </div>
                            </div>

                        </div>
                   
                        </div>
                    </div>


                </div>
                
                

                
            </div>
        )
    }
}

export default login
