import React, { Component } from 'react'
import register from '../auth/register';
import Header from '../header';


const BookingUrl = "https://edurestapi.herokuapp.com/placeorder";
class PlaceBooking extends Component {
    constructor(props)
    {
        super(props);

        this.state={
            hotel_name:this.props.match.params.name,
            name:this.getUserName(''),
            phone:''
        }
    }

    getUserName = (data) =>{
        if(sessionStorage.getItem('username')==undefined
            || sessionStorage.getItem('username')=="undefined"
            || sessionStorage.getItem('username')==null)
            {
                return data;
            }

            return sessionStorage.getItem('username');
    }
    handleSubmit = ()=>{

        fetch(BookingUrl,
            {
                method:'POST',
                headers:{
                    'Accept':'application/json',
                    'Content-Type':'application/json'
                },
                body:JSON.stringify(this.state)
            })
        
        alert('Thank you for ordering food from us!!')

        this.props.history.push('/');

    }
    handleChange = (event)=>{
        this.setState({[event.target.name]:event.target.value});
    }
    render() {
        return (
            <div>
                <Header />

                <div className="container">

                <br/>
                <div className="panel panel-info">
                    <div className="panel-heading">
                        Place Booking
                    </div>
                    <div className="panel-body">
                        <div className="form-group">
                            <label>Hotel Name</label>
                            <input name="hotel_name" value={this.state.hotel_name} readOnly
                            className="form-control"/>
                        </div>
                        <div className="form-group">
                            <label>Name</label>
                            <input name="name" value={this.state.name}
                            className="form-control"
                            onChange={this.handleChange}/>
                        </div>
                        <div className="form-group">
                            <label>Phone</label>
                            <input name="phone" value={this.state.phone}
                            className="form-control"
                            onChange={this.handleChange}/>
                        </div>
                        <button className="btn btn-success"
                        onClick={this.handleSubmit}>
                            Place Booking
                        </button>
                    </div> 
                </div>
                </div>

            </div>
        )
    }
}

export default PlaceBooking;