
import React from 'react';
import './index.css';
import {Link} from 'react-router-dom';

const QuickSearch = (props)=>{

    const renderList = ({quickdata})=>{
        if(quickdata){
        return quickdata.map((data)=>{
           return (

            <Link to={`/listing/${data._id}`}>

            <div className="col-lg-4 col-md-4 col-sm-6 col-xs-12">

                <div className="card1">

                <div class="row">
                    <div class="col-lg-6 col-md-6 col-sm-6 col-xs-6">
                            <img className="img1" src={`images/${data.name}.png`} alt=""/>
                    </div> 
                    <div class="col-lg-6 col-md-6 col-sm-6 col-xs-6">

                            <div className="card_Info">
                            <h2 className="h2-1">{data.name}</h2>
                            <p class="p-2">Start your day with exclusive {data.name} options</p>
                            </div>
                                  
                    </div>
                </div>


                </div>
                                
            </div>

            </Link>

           );
         });

        }
        else{
            return <img style={{width:'40%',height:'250px',marginLeft:'30%',marginRight:'30%'}} src={`images/loader.gif`} />
        }
    }
    return(
        <>

        <div class="container container-fluid" >
        <h1 class="h1-1">Quick Search</h1>
        <p class="p-1">Discover restaurant by type of meal</p>

        
        <div className="row">
        {renderList(props)}
        </div>
        

        </div>
        </>
    )
}

export default QuickSearch;