
import React, { Component } from 'react';
import { Link, withRouter } from 'react-router-dom';
import './style.css';


class Header extends Component{

    constructor(props)
    {
        super(props)
        
        this.state = {
            username:'',
            imgUrl:''
        }
    }

    
    logout = ()=>{
        sessionStorage.removeItem('username');
        sessionStorage.removeItem('imgUrl');

        if(sessionStorage.getItem('ltk'))
        {
            sessionStorage.removeItem('ltk');
        }

        this.props.history.push('/');
    }


    login = ()=>{
        const code = (this.props.location.search).split('=')[1];

        console.log(code);

        if(code)
        {
            let requestData={
                code:code
            }

            console.log("requestData>>>",requestData)
            fetch('https://loginwithgit.herokuapp.com/users',{
                method:'POST',
                headers:{
                    'Accept':'application/json',
                    'Content-Type':'application/json'
                },
                body: JSON.stringify(requestData)
            })
            .then((res)=>res.json())
            .then((data)=>{
                
                console.log(data);
                var user = data.login;
                var img = data.avatar_url;
                sessionStorage.setItem('username',user);

                this.setState(
                {
                    username:user,
                    imgUrl:img
                })
            }
            )
        } 


    }

    conditionalHeader = ()=>{
        if(sessionStorage.getItem('username')==null ||
            sessionStorage.getItem('username')==undefined ||
            sessionStorage.getItem('username')=="undefined"
        )
        {   console.log('>>>>a>>',sessionStorage.getItem('username'))
            return (
                <div class="menu">
                    <Link to="/login" className="login">Login &nbsp;&nbsp;</Link>
                    <Link to="/register" className="btn" id="account"> Create an Account</Link>
                    <a onClick={this.login} className="btn btn-success" style={{borderRadius:'44px'}} href='https://github.com/login/oauth/authorize?client_id=92bbe6baf02b48b27a97'>
                            Login With Github
                    </a>
                    

                </div>
            );
        }
        else{
            return (

                <div style={{color:'white'}} class="menu">

                    <img src={sessionStorage.getItem('imgUrl')} style={{height:35,width:35,borderRadius:'50%',border:'3px solid white',marginRight:'20px'}}/>
                    Hi {sessionStorage.getItem('username')}

                    <div class="dropdown" style={{float:'right',marginLeft:'20px'}}>
                        <button class="btn btn-warning dropdown-toggle" type="button" data-toggle="dropdown">More actions
                        <span class="caret"></span></button>
                        <ul class="dropdown-menu">
                            <li><Link to={`/orders/${sessionStorage.getItem('username')}`}>My Orders</Link></li>
                            <li><Link onClick={this.logout}>Log Out</Link></li>
                            
                        </ul>
                    </div>
                </div>
                
            )
        }
    }

    dataPush=()=>{
        sessionStorage.setItem('username',this.state.username)
        this.props.history.push('/')
    }


    render(){

        return (
            <React.Fragment>
                    <header>
            <div class="header">
               
               
    
                
                
                <Link id="logo" onClick={this.dataPush}>e!</Link>  
                    
                
                
                
                {this.conditionalHeader()}
                
                
                <div style={{clear:'both'}}></div>
            </div>
        </header>
    
    
    
            </React.Fragment>
        );

    }

    
    componentDidMount(){
        
    }

    
}

export default withRouter(Header) 