import axios from 'axios';
import React from 'react';
import Header from '../header';
import Detail from './detail';


let url = "https://edurestapi.herokuapp.com/rest"

class DetailApi extends React.Component{

    constructor(){

        super()
        

        this.state = {
            rest:''
        }

        
        
    }

    render(){
        return (
            <>
                <Header/>
                <Detail restdata={this.state.rest}/>
                
            </>
        );
    }

    componentDidMount(){
        var mealid = this.props.match.params.id;
        console.log(mealid);
        axios.get(`${url}/${mealid}`)
        .then((response) => {

            console.log(response.data[0]);
            this.setState({rest:response.data[0]});
            
        }) 
       
    }
}


export default DetailApi