import React from 'react';
import { BrowserRouter, Route} from 'react-router-dom';
import Home from './Home/Home';
import Listing from './Listing/listing'
import Detailing from './Detailing/detailApi'
import Booking from './Booking/placeBooking';
import PersonelOrders from './Booking/personelOrder';
import Login from './auth/login';
import Register from './auth/register';

const Routing = ()=>{
    return(
        

            <BrowserRouter>
                <Route exact path="/" component={Home}/>
                <Route path="/listing/:topic" component={Listing}/>
                <Route path="/detailing/:id" component={Detailing}/>
                <Route path="/booking/:name" component={Booking}/>
                <Route path="/orders/:name" component={PersonelOrders}/>
                <Route path="/login" component={Login}/>
                <Route path="/register" component={Register}/>
            </BrowserRouter>

       
    )
}



export default Routing;