import React from 'react';
import Header from '../header';
import axios from 'axios';
import List from './List';
import CostFilter from '../Filters/CostFilter';
import SortFilter from '../Filters/Sort';
import CuisineFilter from '../Filters/CuisineFilter';

let url = "http://edurestapi.herokuapp.com/rest?mealtype="


class Listing extends React.Component{

    constructor(props){
        super(props)

        this.state = {
            restlist:''
        }
    }

    setDataPerFilter=(sortedData)=>{
        this.setState({restlist:sortedData})
    }
    

    pageDataRender = (data)=>{
        console.log('>>>>>>',data);
        if(data!=undefined)
        {
            return data.map((item)=>{
                if(this.state.restlist.current_page==item)
                {
                    return (<button style={{marginLeft:'10px'}} className="btn btn-success" value={item-1} onClick={this.changePage}>{item}</button>)
                }
                else
                {
                    return (<button style={{marginLeft:'10px'}} className="btn btn-danger"  value={item-1} onClick={this.changePage}>{item}</button>)
                }
                
            })
        }
    }

    total_length = (data)=>{
        if(data!=undefined)
        {
            return data.length;
        }
        
    }

    changePage = (event)=>{
        var value = event.target.value;
        var mealid = sessionStorage.getItem('mealId',mealid);
        sessionStorage.setItem('page',value)
        
        axios.get(`${url}${mealid}&page=${(value)}`)
        .then((response) => {
            console.log(response.data);
            this.setState({restlist:response.data})})
    }


    
    previos=(data)=>{
        if(data!=undefined)
        {
            return (<button title="Previous" className="btn" value={Math.max(data-2,0)} onClick={this.changePage}>&#60;&#60;</button>)
        }
    }

    next=(data)=>{
        if(data!=undefined)
        {
            return (<button title="Next" style={{marginLeft:'10px'}} className="btn" value={Math.min(data,this.total_length(this.state.restlist.total_pages)-1)} onClick={this.changePage}>&#62;&#62;</button>)
        }
    }



    render(){

        return(
            <>
                <Header />

                <div id="main">

            
                    <h2 id="heading" style={{fontSize: 'xx-large'}}> Search restaurant </h2>


                    
                    <div id="first">
                        <h3 id="heading">Filters</h3>
                        
                        

                        <CuisineFilter restPerCuisine={(data) => {this.setDataPerFilter(data)}} />


                        <CostFilter restPerCost={(data) => {this.setDataPerFilter(data)}} />
                        
                        <SortFilter restPerSort={(data) => {this.setDataPerFilter(data)}} />


                    </div>


                    <div id="second">
                    
                        <List listdata={this.state.restlist}/>


                        <div style={{textAlign:'center'}}>
                            <h5> Page {this.state.restlist.current_page} </h5>
                            {this.previos(this.state.restlist.current_page)}
                        {this.pageDataRender(this.state.restlist.total_pages)}
                        {this.next(this.state.restlist.current_page)}
                        </div>

                    </div>

                </div>


                
            </>
        );

    }

    


    componentDidMount(){
        var mealid = this.props.match.params.topic;
        sessionStorage.setItem('mealId',mealid);
        
        axios.get(`${url}${mealid}`)
        .then((response) => {
            console.log(response.data);
            this.setState({restlist:response.data})})

    }
    
}

export default Listing


