import React from 'react';
import './index.css';
import {Link, withRouter} from 'react-router-dom';
let url = "https://edurestapi.herokuapp.com/city";

let rurl = "https://edurestapi.herokuapp.com/rest?city=";

let infoUrl = "https://edurestapi.herokuapp.com/userInfo"

class Search extends React.Component{

    constructor(props){
        super(props);

        this.state = {
            city:'',
            rest:'',
            username:'',
            imgUrl:''
        }
    }

    renderCity = (data)=>{
        
        if(data){
            return data.map((item)=>{
                return (
                 <option value={item.city}> {item.city_name}</option>
                 );
            })
        }
       
    }

    handleCity = (event)=>{
        const cityId = event.target.value;
        fetch(`${rurl}${cityId}`,{method:'GET'})
        .then((res)=> res.json())
        .then((data) => {
            this.setState({rest:data})
        })
    }


    renderRest = (data) => {
        if(data){
            return data.map((item) => {
                return(
                    <option value={item._id}>{item.name} | {item.locality}</option>
                )
            })
        }
    }


    logout = ()=>{
        sessionStorage.removeItem('username');
        sessionStorage.removeItem('imgUrl');

        if(sessionStorage.getItem('ltk'))
        {
            sessionStorage.removeItem('ltk');
        }

        this.props.history.push('/');
    }


    conditionalButton =() =>{
        
        console.log(">>>state",this.state)
        if(sessionStorage.getItem('username') == null || sessionStorage.getItem('username') == undefined || this.state.username==undefined ||this.state.username==''){
            return(
                <div class="menu">
                    <Link to="/login" className="login">Login </Link>&nbsp;&nbsp;
                    <Link to="/register" style={{border:'2px solid white',color:'white'}}  className="btn btn-light" > Create an Account</Link>
                    &nbsp;&nbsp;
                    <a onClick={this.login} className="btn btn-success" style={{borderRadius:'44px'}} href='https://github.com/login/oauth/authorize?client_id=92bbe6baf02b48b27a97'>
                        Login With Github
                    </a>
                

                </div>
            )
        }
        else{
            return(
                <div style={{marginTop:'30px',color:'white',fontWeight:'bold',fontSize:'20px'}}>
                    <img src={this.state.imgUrl} style={{height:60,width:60,borderRadius:'50%',border:'3px solid white',marginRight:'20px'}}/>
                    Hi {sessionStorage.getItem('username')}

                    <div class="dropdown" style={{display:'inline',marginLeft:'20px'}}>
                        <button class="btn btn-warning dropdown-toggle" type="button" data-toggle="dropdown">More actions
                        <span class="caret"></span></button>
                        <ul class="dropdown-menu">
                            <li><Link to={`/orders/${sessionStorage.getItem('username')}`}>My Orders</Link></li>
                            <li><Link onClick={this.logout}>Log Out</Link></li>
                            
                        </ul>
                    </div>

                </div>
            )
            
        }
    }



    handleRest=(event)=>{
        this.props.history.push(`/detailing/${event.target.value}`)
    }

    render(){
        console.log('>>>>>',this.props)
        return (
            <>

<nav className="navbar image">
            
<div className="container-fluid">
    <div style={{float:'right'}}>
        {this.conditionalButton()}
    </div>
    
   
<div className="row">

        <div className="logo1 center-block">
                <p>e!</p>  
        </div>
</div>


<div class="row">
        
        <div class="center-block">
                <h1 class="heading">Find the best restaurants, cafés and bars</h1> 
        </div>
</div>
<div class="form-1 center-block text-center"> 

        <form action="" >
           <div class="row">
                  <div class="form-group col-md-3 col-sm-3"></div>
                   <div class="form-group form-group-lg col-md-2 col-sm-2">
                           
                         <select style={{borderRadius: '0%'}} onChange={this.handleCity} className="form-control input1" id="location">
                             <option>  LOCATION  </option>
                            {this.renderCity(this.state.city)}
                         </select>
                         
                   </div>
                
                   <div class="form-group form-group-lg col-md-4 col-sm-4">

                        <div class="input-group">
                         
                         <span class="input-group-addon white">
                                 <span class="glyphicon glyphicon-search"></span>
                         </span>       

                         <select class="form-control left-border-none input1" style={{borderRadius: '0%'}} onChange={this.handleRest}  id="location">
                             <option>  Restaurant  </option>
                            {this.renderRest(this.state.rest)}
                         </select>
                        
                        
                        </div>
                        
                        

                   </div>
           </div>
        </form>
</div>
</div>


</nav>

            </>
        )
    }


    componentDidMount(){

        const code = (this.props.location.search).split('=')[1];

        console.log(code);

        if(code)
        {
            let requestData={
                code:code
            }

            console.log("requestData>>>",requestData)
            fetch('https://loginwithgit.herokuapp.com/users',{
                method:'POST',
                headers:{
                    'Accept':'application/json',
                    'Content-Type':'application/json'
                },
                body: JSON.stringify(requestData)
            })
            .then((res)=>res.json())
            .then((data)=>{
                
                console.log(data);
                var user = data.login;
                var img = data.avatar_url;
                sessionStorage.setItem('username',user);
                sessionStorage.setItem('imgUrl',img);
                this.setState(
                {
                    username:user,
                    imgUrl:img
                })
            }



                
            )

            this.props.history.push('/');
        } 


        if(sessionStorage.getItem('token'))
        {
            fetch(infoUrl,{
                method:'GET',
                headers:{
                    'x-access-token':sessionStorage.getItem('ltk')
                }
            })
            .then((res) => res.json())
            .then((data) => {
                
                sessionStorage.setItem('username',data.username);
                sessionStorage.setItem('imgUrl',data.avatar_url);
                console.log('>>imf',data);
                this.setState({
                    username:data.username,
                    imgUrl:data.avatar_url
                })
            })
        }
        
        
        
        fetch(url,{method:'GET'})
        .then((res)=>res.json())
        .then((data)=>this.setState({city:data}));
    }
}

export default withRouter(Search) 