import axios from 'axios';
import React, { Component } from 'react'
import Header from '../header';


let url = "https://edurestapi.herokuapp.com/orders/"

class personelOrder extends Component {
    constructor(props)
    {
        super(props)

        this.state = {
            orders:''
        }
    }

    renderData = (data)=>{
        if(data!='')
        {
            return data.map((item)=>{
                console.log(item);
                return (
                    <>
                        <tr>
                            <td>{item.name}</td>
                            <td>{item.hotel_name}</td>
                            <td>{item.phone}</td>
                        </tr>
                    </>
                )
            })
        }
        else
        {
            return (<>
                <img src={`https://cdn.dribbble.com/users/100142/screenshots/2920352/loading-animation-cd-v2.gif`} style={{marginLeft:'auto',marginRight:'auto'}} />
            </>);
        }
    }

    render() {
        return (
            <div>
                <Header />

                <div className="container">
                    

                    <table class="table table-striped">
                        <thead>
                        <tr>
                            <th>Username</th>
                            <th>Hotel name</th>
                            <th>Phone no</th>
                        </tr>
                        </thead>
                        <tbody>
                            {this.renderData(this.state.orders)}
                        </tbody>
                    </table>
                </div>
                
            </div>
        )
    }

    componentDidMount()
    {
        var name = this.props.match.params.name;

        axios.get(`${url}${name}`)
            .then((resp)=>{
                this.setState({orders:resp.data})
            })
    }
}

export default personelOrder
