import React, { Component } from 'react'
import Header from '../header'

let url = "https://edurestapi.herokuapp.com/register";


class register extends Component {

    constructor(props){
        super(props)

        this.state ={
            username:'',
            email:'',
            password:'',
            avatar_url:'https://library.kissclipart.com/20180905/arq/kissclipart-businessman-icon-png-clipart-computer-icons-user-p-f040ea1493575c42.jpg'
        }
    }

    handleChange=(event)=>{
        this.setState({
            [event.target.name]:event.target.value
        })
        
    }
    handleSubmit = ()=>{
        console.log('>>>',this.state)

        
            fetch(url,
                {
                    method:'POST',
                    headers:{
                        'Accept':'application/json',
                        'Content-Type':'application/json'
                    },
                    body:JSON.stringify(this.state)
                })
            .then((resp)=>resp.json())
            .then((data)=>{
                sessionStorage.setItem('token',data.token);
                this.props.history.push('/login');
            })



        
        

        

    }
    render() {
        return (
            <div>
                <Header />
                <br/><br/>
                <div className="container">
                <div className="row">
                    <div className="col-md-6 col-sm-6 col-xs-12">
                            <img style={{width:'100%'}} src={`https://miro.medium.com/max/498/1*Es2vCEieszU7QQqsLChl6A.png`} />
                    </div>

                    <div className="col-md-6 col-sm-6 col-xs-12">
                        

                        <div className="panel" style={{boxShadow:'0 3px 6px 0 rgba(0, 0, 0, 0.26)'}}>
                            <div className="panel-heading" style={{backgroundColor:'#eb361e',color:'white'}}>Register New User</div>
                            <div className="panel-body">
                                <div>
                                    <div className="form-group">
                                        <label for="username">Username *</label>
                                        <input type="text" name="username" value={this.state.username} className="form-control" onChange={this.handleChange} required/>
                                    </div>
                                    <div className="form-group">
                                        <label for="email">Email *</label>
                                        <input type="email" name="email" value={this.state.email} className="form-control" onChange={this.handleChange} required/>
                                    </div>
                                    <div className="form-group">
                                        <label for="password">Password *</label>
                                        <input type="password" name="password" value={this.state.password} className="form-control" onChange={this.handleChange} required/>
                                    </div>
                                    <div className="form-group">
                                        <label for="password">Confirm Password *</label>
                                        <input type="text" name="c_password" value={this.state.c_password} className="form-control" onChange={this.handleChange} required/>
                                    </div>

                                    <div className="form-group">
                                    
                                        <button onClick={this.handleSubmit} className="form-control btn btn-danger btn-block" >Login</button>
                                    </div>

                        
                                </div>
                            </div>

                        </div>
                   
                        </div>
                    </div>


                </div>
                
                

                
            </div>
        )
    }
}

export default register
