import React,{Component} from 'react';
import QuickSearch from './QuickSeach';

let url = "https://edurestapi.herokuapp.com/meal";

class QuickApi extends Component{

    
    constructor(){
        super();

        this.state = {
            meal:''
        }
    }

    render(){
        return ( 
            <>
                <QuickSearch quickdata={this.state.meal} />
            </>
        )
    }

    componentDidMount()
    {
        fetch(url,{method:'GET'})
        .then((res)=>res.json())
        .then((data)=>{this.setState({meal:data})})
        
    }
}

export default QuickApi;