import React from 'react';

import {Link} from 'react-router-dom';



const List = (props)=>{

    
    
   


    const renderList = (data)=>{
        if(data){

            if(data.length==0)
            {
                return (
                    <div className="row" >
                        <div className="col-md-2 col-sm-2 col-xs-2"></div>
                        <div className="col-md-8 col-sm-8 col-xs-8">
                        <h3>No data found</h3>

                        <img style={{width:'100%'}} src={'https://www.pharmalookup.com/images/noresult.gif'} />
                        </div>
                        <div className="col-md-2 col-sm-2 col-xs-2"></div>


                </div>
                )
            }
            else{
                return data.map((item)=>{
                    return (
                        
                    <div id="cardContainer">
    
                        
                    <div class="cards">
                        <div class="floatLeft">
                            <img className="img" src={item.thumb} alt="" />
                        </div>
                        <div class="floatLeft">
                            
                            <h2  style={{marginBottom: '0px', marginTop:'-1px'}}>
                                
                                <Link id="heading" to={`/detailing/${item._id}`}>{item.name}</Link> 
                                
                            </h2>
                            
                            <p className="p" style={{marginBottom: '2px'}}>{item.city_name}</p>
                            <span>{item.locality}</span>
                        </div>
                        <div style={{clear:'both'}}></div>
                        <hr class="Path-6195" />
        
        
                        <div class="floatLeft" style={{paddingTop: '10px'}}>
                            <p style={{fontWeight: 'normal'}}><span>CUISINES</span></p>
                            <p style={{fontWeight: 'normal'}}><span>COST FOR TWO</span></p>
                            
                        </div>
        
                        
                        <div class="floatLeft" style={{paddingTop: '10px'}}>
                            <p>{item.type[0].name}</p>
                            <p>&#x20B9; {item.cost}</p>
                        </div>
        
                        <div style={{clear:'both'}}></div>
        
                    </div>
        
                    
                </div>
        
               
                    );
                });
            }
           
        }
        else
        {
            return (
                <div className="row">
                        <div className="col-md-2 col-sm-2 col-xs-2"></div>
                        <div className="col-md-8 col-sm-8 col-xs-8">
                        <img style={{width:'100%',height:'500px'}} src={'https://freight.cargo.site/w/750/i/b13cc96c354d94fcf7731df36fe448f29306ac72db70307b30679dbd81b73f3f/Eyck_Branded_Loader_Loop_1.gif'} />
                        </div>
                        <div className="col-md-2 col-sm-2 col-xs-2"></div>
                        
                </div>
            )
        }
        
        
        
    }


    return (
        
        
            <>
                    
                    

                {renderList(props.listdata.data)}

                

                
        
            </>
    );

}

export default List