import axios from 'axios';
import React, { Component } from 'react';

let rest = `https://edurestapi.herokuapp.com/rest?mealtype=${sessionStorage.getItem('mealId')}`;



class CostFilter extends Component {

    constructor(props)
    {
        super(props);
    }

    renderdata = (event) =>{

        
        let cost = (event.target.value).split(',');
        let lcost = cost[0];
        let hcost = cost[1];
        let costUrl,Url;

        if(sessionStorage.getItem('location')!=null)
        {
            costUrl=`${rest}&city=${sessionStorage.getItem('location')}`
        }
        else{
            costUrl=rest
            
        }

        console.log(costUrl)

        if(cost==''){
            Url=costUrl;
        }else{
            Url=`${costUrl}&lcost=${lcost}&hcost=${hcost}`
            
        }
        axios.get(Url)
            .then((response) => {this.props.restPerCost(response.data)})

    }
    
    
    render() {
        return (
            <div>
                
                <br/>
                <p className="p">Cost for Two</p>

                <div style={{padding:'20px',paddingTop:'0px',paddingBottom:'0px'}} onChange={this.renderdata}>

                    <label className="radio">    
                        <input type="radio" value="0,6000"  name="cost" />&nbsp;<span> All </span><br/>
                    </label>
                    <label className="radio">
                        <input type="radio" value="0,200"  name="cost"/>&nbsp;<span>Less than &#x20B9; 200</span><br/>
                    </label>
                    <label className="radio">
                        <input type="radio" value="201,400" name="cost"/>&nbsp;<span>&#x20B9; 201 to &#x20B9; 400</span><br/>
                    </label>
                    <label className="radio">
                        <input type="radio" value="401,600" name="cost"/>&nbsp;<span>&#x20B9; 401 to &#x20B9; 600</span><br/>
                    </label>
                    <label className="radio">
                        <input type="radio" value="601,800" name="cost"/>&nbsp;<span>&#x20B9; 601 to &#x20B9; 800</span><br/>
                    </label>
                    <label className="radio">
                        <input type="radio" value="801,6000" name="cost"/>&nbsp;<span>Above &#x20B9; 801</span><br/>
                    </label>

                </div>

            </div>
        )
    }


}

export default CostFilter
