import React from 'react';
import { Link } from 'react-router-dom';

import { Tab, Tabs, TabList, TabPanel } from 'react-tabs';
import 'react-tabs/style/react-tabs.css';


const Detail = (props)=>{

    var img={
        width:'100%',
        height:'250px',
        marginTop:'30px',
        borderRadius:'1%'
    }

    
    const renderCui =(data)=>{

        if(data){
            return data.map((item)=>{
                console.log(props);
                return (
                    <span> &#9733;  {item.name} &nbsp;&nbsp;&nbsp; </span>
                );
            })
        }

    }

    
        

    return (
            <>
                 <div className="container">
            
            <br/>

            <div className="panel panel-primary">
                <div className="panel-heading">
                    {props.restdata.name}
                </div>
                <div className="panel-body">
                    <img style={img}  src={props.restdata.thumb} />
                    <hr className="Path-6195"/>

                    <Tabs>
                        <TabList>
                            <Tab>Overview</Tab>
                            <Tab>Contact</Tab>
                        </TabList>
                        <TabPanel>
                            
                            <h3 className="h1-1" style={{textAlign:'center'}}>{props.restdata.address}</h3>
                            <p className="p-1" style={{textAlign:'center'}}>City : {props.restdata.city_name}</p>
                            <p style={{color:'green',textAlign:'center'}}>&#x20B9; {props.restdata.cost} Per Two Persons</p>
                        </TabPanel>
                        <TabPanel>
                            <h4 style={{textAlign:'center'}} className="h1-1">Contact : {props.restdata.contact_number}</h4>
                            <p style={{textAlign:'center'}} className="p-1">Email : paslas.sa.com</p>
                            <div style={{textAlign:'center'}}>
                            <Link style={{textAlign:'center'}} className="btn btn-success" to={`/booking/${props.restdata.name}`} >Proceed Order</Link>

                            </div>
                                                    
                        </TabPanel>
                    </Tabs>

                </div>
            </div>

            
                

                
                

                
            

        </div>

            </>    
        );
}

export default Detail